@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Oleo+Script|Signika|Nunito:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar ul {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content:flex-end;
    list-style: none;
    padding: 1rem;
    padding-top: 2.5rem;
    padding-bottom: 0;
}

.Navbar li {
  font-family: 'Nunito', sans-serif;
  color: #91d8b5;
  margin-right: 5rem;
  font-size: 1.45rem;
  font-weight: 300;
}

.Title h1 {
  font-size: 5rem;
}

.Title {
  max-width: 800px;
  height: 100%;
  min-height: 650px;
  margin: auto;
  /* border: 1px solid red; */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-bottom: 5rem;
}

.Title-name {
  text-align: center;
  margin: 4rem;
}

.Title-blurb {
  color: #555555;
  font-family: 'Nunito', sans-serif;
  font-size: 1.5rem;
  line-height: 2rem;
  margin: 0 20%;
  text-align: center;
}

@media only screen and (max-width: 650px) {
  .Title h1 {
    font-size: 3rem;
  }
  .Title h2 {
    font-size: 1.5rem;
  }
  .Title-blurb {
    font-size: 1.25rem;
    line-height: 2rem;
  }
}

@media only screen and (max-width: 450px) {
  .Title {
    padding: 1rem;
  }
  .Title-name {
    margin: 1rem;
    font-size: 2rem;
  }
  .Title-blurb {
    font-size: 1.1rem;
    margin: 0 .5rem;
  }
}

.Bubble h2 {
  font-weight: 400;
  font-size: 1.5rem;
}

.Bubble p {
    font-size: 18px;
    padding-top: 1rem;
    line-height: 2.2rem;
    color: #555555;
}
.Bubble-container {
  margin: auto;
  padding: 3rem 6rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  vertical-align: middle;
}

.Bubble-image {
  box-shadow: 0px 25px 20px -9px rgba(0,0,0,0.1);
  overflow: hidden;
  border-radius: 5px;
  width: 150px;
  height: 150px;
  margin-right: 5rem;
  margin-left: 10rem;
  flex-shrink: 0;
}
.Bubble-image.right {
  margin-right: 5rem;
  margin-left: 5rem;
}

.Bubble img {
  max-height: 200px;
}
.Bubble-info {
  max-width: 500px;
  flex-grow: auto;
}
.Bubble-info.right {
  /* margin-left: 10rem; */
}

@media only screen and (max-width: 925px) {
  .Bubble-container {
    padding: 3rem 2rem;
  }

  .Bubble-image {
      margin-left: 0rem;
      margin-right: 3rem;
  }
  .Bubble-image.right {
      margin-left: 3rem;
      margin-right: 0rem;
      margin-bottom: 0;
  }
  .Bubble-info.right {
      margin-left: 0rem;
      margin-right: 0rem;
  }
}
@media only screen and (max-width: 600px) {
  .Bubble-container {
    margin: auto;
    padding: 2rem;
    /* padding: 2rem 20rem; */
    border: 1px solid blue;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
  }

  .Bubble-image {
    margin-right: 0;
    margin-bottom: 2rem;
    box-shadow: none;
  }

  .Bubble-image.right {
    display: none;
    margin-right: 0;
    margin-bottom: 2rem;
    box-shadow: none;
  }
  .Bubble-info {
    text-align: center;
  }

}

